/* import lexer from "lex"; */

import * as moo from "moo";
import DiceRollerPlugin from "src/main";
import { Conditional, Lexeme } from "src/types";
import { Parser } from "./parser";

export const TAG_REGEX =
    /(?:\d+[Dd])?#(?:[\p{Letter}\p{Emoji_Presentation}\w/-]+)(?:\|(?:[+-]))?(?:\|(?:[^+-]+))?/u;
export const LINK_REGEX =
    /(?:\d+[Dd])?#(?:[\p{Letter}\p{Emoji_Presentation}\w/-]+)(?:\|(?:[+-]))?\|link/u;

export const TABLE_REGEX =
    /(?:.*)?(?:\[.*\]\(|\[\[)(?:.+?)#?\^(?:.+?)(?:\)|\]\])\|?(?:.+)?/u;
export const SECTION_REGEX =
    /(?:\d+[Dd])?(?:\[.*\]\(|\[\[)(?:.+)(?:\)|\]\])\|?(?:.+)?/u;
export const LINE_REGEX =
    /(?:\d+[Dd])?(?:\[.*\]\(|\[\[)(?:.+)(?:\)|\]\])\|line/u;
export const MATH_REGEX = /[\^\+\-\*\/]/u;
export const OMITTED_REGEX = /(?:\d+|\b)[Dd](?:-?\d+|%|F|\b)/u;

export const CONDITIONAL_REGEX = /(?:=|=!|<|>|<=|>=|=<|=>|-=|=-)\d+/u;

type RuleTypes =
    | "WS"
    | "multi"
    | "condition"
    | "modifier"
    | "table"
    | "section"
    | "tag"
    | "link"
    | "line"
    | "dice"
    | "math"
    | "sort"
    | "stunt"
    | "%";

interface ConditionedToken extends moo.Token {
    conditions?: Conditional[];
    modifiers?: LexicalToken[];
    endOffset: number;
}
interface DiceToken extends ConditionedToken {
    parenedDice?: boolean;
    type?: RuleTypes;
}
export interface GroupToken extends ConditionedToken {
    type: "group-start" | "group-end" | "group";
    nested: LexicalToken[];
}

export type LexicalToken = DiceToken | GroupToken;

const MODIFIERS = new Set(["kl", "kh", "dh", "dl", "!!", "!", "r", "sort"]);

export default class Lexer {
    lexer: moo.Lexer;
    parser: Parser;
    clampInfinite(match: string) {
        if (/i$/.test(match)) return "100";
        return match.replace(/^\D+/g, "");
    }
    constructor(public plugin: DiceRollerPlugin) {
        const exponent = {
            precedence: 3,
            associativity: "right"
        };

        const factor = {
            precedence: 2,
            associativity: "left"
        };

        const term = {
            precedence: 1,
            associativity: "left"
        };

        this.parser = new Parser({
            "+": term,
            "-": term,
            "*": factor,
            "/": factor,
            "^": exponent
        });

        const rules: moo.Rules = {
            WS: [{ match: /[ \t]+/u }],
            table: TABLE_REGEX,
            line: LINE_REGEX,
            section: SECTION_REGEX,
            link: LINK_REGEX,
            tag: TAG_REGEX,

            condition: CONDITIONAL_REGEX,
            kl: { match: /kl\d*/u, value: this.clampInfinite },
            kh: [
                { match: /kh\d*/u, value: this.clampInfinite },
                { match: /k\d*/u, value: this.clampInfinite }
            ],
            dh: { match: /dh\d*/u, value: this.clampInfinite },
            dl: [{ match: /dl\d*/u, value: this.clampInfinite }],
            "!!": {
                match: /!!(?:i|\d+)?/u,
                value: this.clampInfinite
            },
            "!": {
                match: /!(?:i|\d+)?/u,
                value: this.clampInfinite
            },
            r: {
                match: /r(?:i|\d+)?/u,
                value: this.clampInfinite
            },
            sort: [
                {
                    match: /s(?:a|d)*/u,
                    value: (str) => (str == "s" || str == "sa" ? "sa" : "sd")
                }
            ],
            stunt: /1[Dd]S/u,
            "%": /\d+[Dd]\d+%/u,
            dice: [
                {
                    match: OMITTED_REGEX,
                    value: (match) => {
                        const {
                            roll = this.plugin.data.defaultRoll,
                            faces = this.plugin.data.defaultFace
                        } = match.match(
                            /(?<roll>\d+)?[Dd](?<faces>(?:-?\d+|%|F))?/
                        ).groups;
                        return `${roll}d${faces}`;
                    }
                },
                {
                    match: /(?:\d+|\b)[Dd](?:\[(?:-?\d+[ \t]?),[ \t]?(?:-?\d+\]))/u,
                    value: (match) => {
                        const {
                            roll = this.plugin.data.defaultRoll,
                            faces = this.plugin.data.defaultFace
                        } = match.match(
                            /(?<roll>\d+)?[Dd](?<faces>\[(?:-?\d+[ \t]?),[ \t]?(?:-?\d+\]))?/
                        ).groups;
                        return `${roll}d${faces}`;
                    }
                },
                { match: /\d+/u },
                {
                    match: /\b[A-Za-z][A-Za-z0-9_]+\b/u,
                    value: (match) => {
                        if (this.plugin.inline.has(match)) {
                            return `${this.plugin.inline.get(match)}`;
                        }
                        return match;
                    }
                }
            ],
            math: MATH_REGEX,
            paren: /[\(\)]/u
        };
        this.lexer = moo.states({
            main: { ...rules, "group-start": { match: /\{/u, push: "group" } },
            group: {
                ...rules,
                math: /[\^\+\-\*\/,]/u,
                "group-end": { match: /\}/u, pop: 1 },
                "group-start": { match: /\{/u, push: "group" }
            }
        });
    }
    parse(input: string) {
        const parsed = this.lexer.reset(input);
        const tokens = Array.from(parsed);
        this.lexer.reset();
        const transformed = this.transform(tokens);

        let ending: LexicalToken[] = [];
        this.recurseTokens(transformed, ending, 0);
        ending = this.parser.parse(ending);
        return ending;
    }
    transform(tokens: moo.Token[]): LexicalToken[] {
        let filtered: LexicalToken[] = tokens.filter((token) => {
            return token.type != "WS";
        }) as LexicalToken[];
        let lastToken: LexicalToken;
        for (const token of filtered) {
            if (token.type == "condition" && lastToken != null) {
                if (!lastToken.conditions) lastToken.conditions = [];
                const [_, operator, comparer] =
                    token.value.match(
                        /(?<operator>=|=!|<|>|<=|>=|=<|=>|\-=|=\-)(?<comparer>\d+)/
                    ) ?? [];
                lastToken.conditions.push({
                    operator,
                    comparer: Number(comparer),
                    value: token.value
                });
                continue;
            }
            lastToken = token;
        }

        return filtered;
    }
    getTypeFromLexemes(lexemes: Lexeme[]) {
        if (lexemes.some(({ type }) => type === "table")) {
            return "table";
        }
        if (lexemes.some(({ type }) => type === "section")) {
            return "section";
        }
        if (lexemes.some(({ type }) => type === "tag")) {
            return "tag";
        }
        if (lexemes.some(({ type }) => type === "link")) {
            return "link";
        }
        if (lexemes.some(({ type }) => type === "line")) {
            return "line";
        }
        return "dice";
    }
    recurseTokens(
        tokens: LexicalToken[],
        parent: LexicalToken[],
        start: number
    ): number {
        let lastToken: LexicalToken;
        for (let i = start; i < tokens.length; i++) {
            const token = tokens[i];

            if (MODIFIERS.has(token.type) && lastToken != null) {
                if (!lastToken.modifiers) lastToken.modifiers = [];
                lastToken.modifiers.push(token);
                continue;
            }
            if (token.type == "group-start") {
                token.nested = [];
                token.type = "group";
                i = this.recurseTokens(tokens, token.nested, i + 1);
                token.nested = this.parser.parse(token.nested);
                token.endOffset = tokens[i].offset;
            } else if (token.type == "group-end") {
                return i;
            } else {
                token.endOffset = token.offset + token.text.length;
            }
            lastToken = token;
            parent.push(token);
        }
    }
}
